.section-category-slider {
	padding-top: 42px;
	padding-bottom: 58px;
	border-bottom: 1px solid #dee6e9;
	border-top: 1px solid #dee6e9;

	&__container {
		max-width: 100%;
	}

	&__title {
		margin-left: -3px;
		margin-bottom: 17px;
	}
}

.category-slider {
	//overflow: hidden; //      
	margin-left: -1px;
	padding-right: 4px;

	z-index: 1;

	&__arrows{
		position: relative;
		@media screen and (min-width: 1151px) {
			&::before {
				z-index: 1;
				content: '';
				display: block;
				z-index: 1;
				position: absolute;
				left: -40px;
				top: 0;
				height: 100%;
				width: calc(100% + 80px);
				//background: red;
			}
		}
		&:hover {

			.category-slider-arrow {
				opacity: 1;
			}
		}
	}

	&__overflow{
		overflow: hidden;
		@media screen and (min-width: 1151px) {
			margin-right: 2px;
		}
	}

	.slick-list {
		z-index: 2;
		overflow: visible;
		@media screen and (min-width: 1151px) {
			width: 788px;
		}
		//	margin: 0 -18px ;
	}

	.slick-dots {
		position: relative;
		z-index: 2;
		margin-top: 36px;
	}




}

.category-slider-arrow {
	z-index: 3;
	//opacity: 1;
	top: 54px;
	cursor: pointer;

}

.category-slide {
	width: 227px;
	/*margin: 0 18px;old*/
	margin: 0 36px 0 0;
	display: flex;
	justify-content: center;
	align-items: center;

	&__img-wrapper {
		max-width: 228px;
		position: relative;
		display: block;
		overflow: hidden;
		 -webkit-mask-image: -webkit-radial-gradient(white,#000); border-radius: 6px;


		&::after {
			content: '';
			display: block;
			padding-top: 57.71%;
		}
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
	}

	&__category-name {
		position: absolute;
		font-size: 22px;
		line-height: 1.2;
		text-align: center;
		letter-spacing: -0.03em;
		color: $dark;
		z-index: 99;
		//background: #fff;
		padding: 14px 20px 10px 10px;
		max-width: 100%;
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}