.page-tabs-section{
  position: relative;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(25px);

  &__container{
    overflow: auto;
    width: 100%;

    @media screen and (max-width: 749px) {
      overflow: hidden;
    }

    &::-webkit-scrollbar {
      width: 0;
      background: transparent;
      height: 0;
      display: none;
    }
    &::-webkit-scrollbar-thumb {
      background: #FF0000;
      height: 0;
      width: 0;
      display: none;
    }
  }
}
.page-tabs{
	margin: 0 auto;
  @media screen and (max-width:1150px) {
    padding-left: calc((100% - 696px)/2);
    padding-right: calc((100% - 696px)/2);
    width: max-content;
    box-sizing: content-box;
  }
  @media screen and (max-width: 749px) {
    padding-left: 3.88889vw;
    padding-right: 3.88889vw;
  }
  &__inner{
    display: flex;
    justify-content: center;
    position: relative;
    @media screen and (max-width:1150px) {
      justify-content: flex-start;
    }
  }
  &__line{
    position: absolute;
    height: 2px;
    width: 0;
    border-radius: 5px;
    background: #6974A9;
    left: calc((100% - 1072px)/2);
    bottom: 0;
    transition: left .3s linear, width .3s linear;
    @media screen and (max-width:1150px) {
      left: calc((100% - 696px)/2);
    }
    @media screen and (max-width: 749px) {
     left: 3.88889vw;
    }
  }

  &__item{
    white-space: nowrap;
    padding: 17px 36px 16px;
    font-size: 17px;
    line-height: 26px;
    display: flex;
    align-items: center;
    color: #53565A;
    position: relative;
    cursor: pointer;
    user-select: none;
    transition: color 0.3s linear;
		-webkit-tap-highlight-color: rgba(0,0,0,0);

    @media screen and (max-width:1150px) {
      font-size: 15px;
      padding: 17px 22px 16px;
    }

    @media screen and (max-width: 749px) {
      padding: vw(14) vw(22) vw(14);
      font-size: vw(15);
      line-height: vw(26);
      font-weight: 500;
    }

    /*&::after{
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background: #6974A9;
      border-radius: 5px;
      transition: opacity 0.4s;
      opacity: 0;
      position: absolute;
      left: 0;
      bottom: 0;
    }*/
    &:hover{
      /*&::after{
        opacity: 0.5;
      }*/
    }
    &--active{
      color: #25378D;
      &::after{
        opacity: 1 !important;
      }
    }
  }
}

.page--tabs-blocks {
	padding-top: 27px;

	@media screen and (max-width: 1150px) {
		padding-top: 35px;
	}

	@media screen and (max-width: 749px) {
		padding-top: vw(35);
	}

	&__tab {
		display: none;
		opacity: 0;

		&--active {
			display: block;
			opacity: 1;
		}
	}
}

