input,
button,
textarea,
select {
	font-size: inherit;
	font-family: inherit;
	margin: 0;
	padding: 0;
	border: 0;
}

button {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
	outline: none;
	font-size: inherit;
	background: transparent;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

* {
	box-sizing: border-box;
}

a,
a:hover {
	text-decoration: none;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

textarea {
	resize: none;
}

a {
	color: inherit;
}


body {
	display: flex;
	flex-direction: column;
	margin: 0;
	color: $dark;
	background: #ffffff;
	font-family: $mainFont;
	font-size: 16px;
}

.main {
	flex-grow: 1;
}

html,
body {
	height: 100%;
}

.clearfix { clear: both; }

.container {
	box-sizing: border-box;
	width: 1200px;
	max-width: 95%;
	max-width: calc(100% - 30px * 2);
	margin: 0 auto;
}

.slick-dots li:first-child:last-child{
	opacity: 0 !important;
}

//    1151px 

@media screen and (max-width: 1150px){//   
	.container{
		width: 582px;
		max-width: calc(100% - 25px * 2);
	}
	.container--long{//    .container  .hr     
		width: 697px;
	}
}

//     


@media screen and (max-width: 749px ){// 767  .     .      360
	.container{// -    -      
		width: vw(337);
		max-width: calc(100% - #{vw(12)} * 2);
	}
	.container--hr{//    .container  .hr
		width: vw(337);
	}
}



#jGrowl {
	z-index: 999999;
}

*:focus {
	outline: none;
}

.hide {
	display: none;
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-font-smoothing: antialiased;
	-o-font-smoothing: antialiased;

	-webkit-locale: auto;
	white-space: normal;

	&.compensate-for-scrollbar {
		overflow: hidden;
	}
}

html {
	-moz-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-o-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	text-size-adjust: 100%;
}

.hr {
	background: #DEE6E9;
	 -webkit-mask-image: -webkit-radial-gradient(white,#000); border-radius: 6px;
	height: 1px;
	width: 100%;
}

//START  

ul li, ol li {
    padding: 0px 0;
}
//END  
.slick-dots {
	li {
		cursor: pointer;
	}
}
.slick-arrow {
	cursor: pointer;
}


.oh-wrap{
	overflow: hidden;
}

.ui-loader.ui-corner-all.ui-loader-default.ui-body-a{
	display: none !important;
}

